:root {
  --primary-color: salmon;
  --secondary-color: salmon;
  --tertiary-color: #eeeeee;
  --primary-font-color: white;
  --secondary-font-color: black;
  --tertiary-font-color: #eeeeee;
  --primary-font: Arial, sans-serif;
  --secondary-font: Arial, sans-serif;
  --font-family: Arial, sans-serif;
  --header-padding: 1em;
  --footer-height: 50px;
}
body {
  margin: 0;
  background-color: var(--primary-color);
  font-family: Arial, sans-serif;
}

.header {
  background-color: var(--primary-color);
  color: var(--primary-font-color);
  text-align: center;
  padding: 2em;
  position: relative; /* Allow child elements to position themselves relative to the header */
}
.header-content {
  display: flex;
  justify-content: space-between; /* Push title to the left and user info to the right */
  align-items: center; /* Vertically align items */
}

.header-title {
  font-size: 1.5em;
  margin: 0;
}

.header-user {
  display: flex;
  flex-direction: column; /* Stack the title and user info vertically */
  align-items: flex-start; /* Align text to the left */
}
.home-icon {
  position: absolute; /* Position the icon relative to the header */
  left: 50%; /* Center horizontally */
  transform: translate(-50%, -50%); /* Adjust for the icon's dimensions */
  display: flex;
  justify-content: center; /* Center the icon horizontally */
  align-items: center; /* Center the icon vertically */
  width: 50px; /* Set a fixed width for the icon container */
  height: 50px; /* Set a fixed height for the icon container */
  border-radius: 50%; /* Optional: Make the icon container circular */
  background-color: #eeeeee; /* Change background color to #eeeeee */
  text-align: center;
  color: var(--primary-color); /* Use the current text color */
  text-decoration: none; /* Remove the underline */
}

.home-icon:hover {
  color: var(--tertiary-color); /* Change color on hover */
}

.logout-button {
  color: var(--tertiary-color);
  border: none;
  padding: 0.5em 1em;
  cursor: pointer;
  font-size: 1em;
  margin-left: auto; /* Push the button to the right */
}
.footer {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  text-align: center;
  padding: 1em;
  position: fixed;
  width: 100%;
  bottom: 0;
}

